.tb_ap_media_wrap img,
.tb_ap_media_wrap video,
.tb_ap_media_wrap iframe{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#pixel_67 .overlay-main{
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
}

#pixel_67.tb_ap_post_container{
    display: block;
}
#pixel_67 .tb_ap_post_row{
    width: 100%;
    float: left;
}
#pixel_67 .tb_ap_post_small{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
#pixel_67 .tb_ap_post_small .tb_ap_post_wrapper{
    padding: 5px;
    overflow: hidden;
    width: 12.50%;
    height: 300px;
}
#pixel_67.display .tb_ap_post_small.size8x6 .tb_ap_post_wrapper{
    width: 12.50%;
    height: 25vh;
}
#pixel_67.display .tb_ap_post_small.size6x3 .tb_ap_post_wrapper{
    width: 16.66%;
    height: 33.33vh;
}
#pixel_67.display .tb_ap_post_small.size4x2 .tb_ap_post_wrapper{
    width: 25%;
    height: 50vh;
}
#pixel_67.display .tb_ap_post_small.size3x2 .tb_ap_post_wrapper{
    width: 33.33%;
    height: 50vh;
}
#pixel_67 .tb_ap_post_large .tb_ap_post_wrapper{
    width: 100%;
    height: 100%;
    padding: 0px;
}
#pixel_67 .tb_ap_post_wrapper .tb_ap_post_in,
#pixel_67 .tb_ap_post_wrapper .tb_ap_post_media_wrapp,
#pixel_67 .tb_ap_post_wrapper .tb_ap_media_wrap,
#pixel_67 .tb_ap_post_wrapper .tb_ap_media_wrap img,
#pixel_67 .tb_ap_post_wrapper .tb_fc_media_wrap{
    height: 100%;
}

#pixel_67 .tb_ap_post_row:nth-child(even) .tb_ap_post_large{
    float: right;
}
#pixel_67 .tb_ap_post_in:hover .overlay-main{
    display: none;
}
#pixel_67 .tb_ap_post_in:hover .zoom-in-effect .overlay-main{
    display: inherit;
}
#pixel_67 .zoom-in-effect .tb_ap_media_wrap img{
    filter: blur(5px) saturate(2);
    -moz-filter: blur(5px) saturate(2);
    -webkit-filter: blur(5px) saturate(2);
    -ms-filter: blur(5px) saturate(2);
    -o-filter: blur(5px) saturate(2);
    opacity: 0.7;
}

#pixel_67.embed .tb_ap_post_in:hover .overlay-main{
    display: block;
}
#pixel_67.embed .tb_ap_post_in .tb_mwf_contant_wrapper{
    height: 100%;
    padding-top: 0px;
    padding-bottom: 0px;
    display: flex;
    align-items: center;
    text-align: center;
    overflow: hidden;
    justify-content: center;
}
#pixel_67.embed .tb_ap_post_in .tb_mwf_author_wrapper{
    position: absolute;
    bottom: 0px;
    left: 0px;
    padding: 10px;
    margin-bottom: 0px;
    height: 20%;
}

@media screen and (max-width: 1200px) {
    #pixel_67 .tb_ap_post_small .tb_ap_post_wrapper{
        width: 33.33% !important;
        /* height: 50vh !important; */
    }
}
@media screen and (max-width: 992px) {
    #pixel_67 .tb_ap_post_small .tb_ap_post_wrapper{
        width: 50% !important;
        /* height: 33.33vh !important; */
    }
}
@media screen and (max-width: 767px) {
    #pixel_67 .tb_ap_post_small .tb_ap_post_wrapper{
        width: 50% !important;
        /* height: 50vh !important; */
    }
}

@media screen and (max-width: 576px) {
    #pixel_67 .tb_ap_post_small .tb_ap_post_wrapper{
        width: 100%;
    }
}